/* #LINKS */
.link {
  text-decoration: none;
  color: unset;
  font-size: medium;
  font-family: "Familjen Grotesk", sans-serif;
  padding: 0px;
}

.link:hover {
  color: teal;
  font-weight: bolder;
}

.link-highlight {
  color: rgb(35, 187, 207);
}

/* #ICONS */
.icon-highlight--primary {
  color: rgb(35, 187, 207);
}

.icon-highlight--primary:hover {
  color: teal;
  font-weight: bolder;
}

.icon-highlight--tertiary {
  color: brown;
}

.box--center {
  display: block;
  margin: auto;
}

/* #MARGINS */
.margin--sm {
  margin: 0.7em auto 2em;
}

/* Navbar Styles */
.navbar {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #333;
  transition: background-color 0.3s ease;
  color: #fff;
  padding: 1.5rem;
}

.navbar__header {
  display: flex;
  justify-content: center;
  width: 100%;
}

.navbar__title {
  font-size: 1.5rem;
}

.navbar__title-link {
  color: #fff;
  text-decoration: none;
}

.navbar__list {
  display: flex;
  align-items: center;
  justify-content: space-around;
  list-style: none;
  width: 100%;
}

.navbar__item {
  margin: 0 1rem;
  padding: 0.5rem 1rem;
  transition: background-color 0.3s ease;
  padding: 5px 20px;
}

.navbar__item:hover {
  background-color: rgba(126, 120, 120, 0.5);
  border-radius: 5px;
}

.navbar__link {
  color: #fff;
  text-decoration: none;
}

.navbar__icon {
  font-size: 2rem;
}

.navbar__icon-wrapper {
  position: relative;
  display: inline-block;
}

.navbar__favourites-count {
  position: absolute;
  top: -10px;
  right: -10px;
  background-color: red;
  color: white;
  border-radius: 50%;
  padding: 2px 8px;
  font-size: 12px;
  font-weight: bold;
}

.navbar__search-form {
  display: flex;
  align-items: center;
}

.navbar__search-fieldset {
  border: none;
}

.navbar__search-label {
  display: none;
}

.navbar__search-input {
  padding: 0.5rem;
  border: 1px solid #fff;
  border-radius: 5px;
  width: 25rem;
}

/* Responsive Styles */
@media (max-width: 768px) {
  /* .navbar {
