  /* Recipe item styles */
  .recipe-item {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin: 16px;
    transition: transform 0.3s ease;
  }
  
  .recipe-item:hover {
    transform: translateY(-5px);
  }
  
  .recipe-item__image-container {
    position: relative;
    width: 100%;
    height: 200px;
    overflow: hidden;
  }
  
  .recipe-item__image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .recipe-item__title {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    color: #fff;
    text-align: center;
    padding: 8px;
    font-size: 1rem;
  }
  
  .recipe-item__details {
    padding: 16px;
    display: flex;
   justify-content: center;
    align-items: center; /* Needed for the link to stack vertically alongside
    /* place-content: center; */ // Why does this not replace both justify content and align items
    flex-direction: column;
  }
  
  .recipe-item__header {
    margin-bottom: 8px;
  }
  
  .recipe-item__publisher {
    font-size: 0.9rem;
    color: #777;
  }
  
  .recipe-item__id {
    font-size: 0.8rem;
    color: #aaa;
    margin-bottom: 16px;
  }
  
  .recipe-item__link {
    display: inline-block;
  /*   display: block;
    text-align: center; */
    margin-bottom: 16px;
    color: #007bff;
    text-decoration: none;
    font-weight: bold;
  }
  
  .recipe-item__link:hover {
    text-decoration: underline;
  }
  

  .recipe-item__button {
    display: block;
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 10px 20px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s ease;
    margin: auto;
  }
  
  
  .recipe-item__button.favourite {
    background-color: red; /* Color when the recipe is in the favorites list */
  }
  
  .recipe-item__button.favourite:hover {
    background-color: darkred; /* Color when hovered */
  }
  
  .recipe-item__button.not-favourite {
    background-color: green; /* Color when the recipe is not in the favorites list */
  }
  
  .recipe-item__button.not-favourite:hover {
    background-color: darkgreen; /* Color when hovered */
  }
  