/* Favourites.css */

/* General styles */
.favourites {
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 1200px;
    margin: 0 auto;
  }
  
  .favourites__title {
    font-size: 1.5rem;
    color: #333;
    margin-bottom: 20px;
    text-align: center;
  }
  
  /* Content styles */
  .favourites__content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  /* List styles */
  .favourites__list {
    display: flex;
    flex-wrap: wrap;
    gap: 50px;
    list-style: none;
    padding: 0;
    margin: 0;
    justify-content: center;
    align-items: center;
  }
  
  .favourites__item {
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 15px;
    box-sizing: border-box;
    transition: transform 0.2s ease-in-out;
  }
  
  .favourites__item:hover {
    transform: scale(1.02);
  }
  
  /* Clear button styles */
  .favourites__clear-button-wrapper {
    text-align: center;
    margin-top: 20px;
  }
  
  .favourites__clear-button {
    font-size: larger;
    font-weight: bolder;
    margin-top: 1.5rem;
    background-color: red;
    color: #fff;
    border: none;
    border-radius: 4px;
    padding: 10px 30px;
    text-transform: uppercase;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .favourites__clear-button:hover {
    background-color: darkred;
  }
  
  /* Empty state styles */
  .favourites__empty {
    text-align: center;
    padding: 20px;
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 8px;
  }
  
  .favourites__message {
    font-size: 1rem;
    color: #666;
  }
  
  /* Media Queries */

/* For tablets and larger screens */
@media (max-width: 1024px) {
  .favourites__item {
      flex: 1 1 calc(50% - 50px); /* 2 items per row */
  }
}

/* For mobile devices */
@media (max-width: 768px) {
  .favourites__item {
      flex: 1 1 calc(100% - 50px); /* 1 item per row */
  }

  .favourites__title {
      font-size: 1.2rem;
  }

  .favourites__clear-button {
      font-size: medium;
      padding: 8px 20px;
  }

  .favourites__message {
      font-size: 0.9rem;
  }
}
