/* General styles */
body {
    font-family: 'Arial', sans-serif;
    line-height: 1.6;
    margin: 0;
    padding: 0;
    background-color: #f9f9f9;
  }
  
  /* Loading indicator styles */
  .loading-indicator {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: rgba(255, 255, 255, 0.8);
  }
  
  .loading-indicator__message {
    font-size: 1.2rem;
    color: #333;
    text-align: center;
  }
  

  /* Home section styles */
  .food-recipe-app__home {
    padding: 20px;
  }
  
  .search-results__heading {
    font-size: 1.9rem;
    color: #333;
    margin-bottom: 20px;
    text-align: center;
  }

  .search-param {
    font-style: italic;
  }
  
  .recipe-list {
    list-style: none;
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
  }
  
  .recipe-list__item {
    flex: 1 1 calc(33.333% - 32px);
    box-sizing: border-box;
  }
  
  .food-recipe-app__home__empty-state {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60vh;
  }
  
  .food-recipe-app__home__empty-state__message {
    font-size: 1.5rem;
    color: #777;
    text-align: center;
  }
  
  /* For tablets and larger screens */
@media (max-width: 1024px) {
  .recipe-list__item {
      flex: 1 1 calc(50% - 32px); /* 2 items per row */
  }
}

/* For mobile devices */
@media (max-width: 768px) {
  .recipe-list__item {
      flex: 1 1 calc(100% - 32px); /* 1 item per row */
  }

  .search-results__heading {
      font-size: 1.5rem;
  }

  .food-recipe-app__home__empty-state__message {
      font-size: 1.2rem;
  }
}