/* || ##VARIABLES */
/* ##COLORS */
$primary-color: rgb(35, 187, 207);
$secondary-color: teal;
$tertiary-color: brown;

$primary-link-highlight-color: $primary-color;
$secondary-link-highlight-color: $secondary-color;

$primary-icon-highlight-color: $primary-color;
$secondary-icon-highlight-color: $secondary-color;
$tertiary-icon-highlight-color: $tertiary-color;

$primary-border-color: $primary-color;

$primary-nav-bg-color: rgba($primary-color, 0.2);


/* ##FONTS */

/* #TEXT */
$secondary-ff: Georgia, serif;
$tertiary-ff: 'Trebuchet MS', sans-serif;

/* #HEADINGS */
$primary-headings-ff: 'Familjen Grotesk', sans-serif;
$secondary-headings-ff: 'Rubik Moonrocks', cursive;

/* #LINKS */
//$primary-link-ff: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
//$primary-link-ff: 'Familjen Grotesk', sans-serif;
$primary-link-ff: $primary-headings-ff;


/* ##MIXINS */
@mixin flexbox($display: flex,
    $flex-direction: row,
    $justify-content: center,
    $align-items: center,
) {
    display: $display;
    flex-direction: $flex-direction;
    justify-content: $justify-content;
    align-items: $align-items;
}